.titles {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family: "Exo 2";
    line-height: 1.2;
    letter-spacing: 5px;
    font-weight: 400;
    font-size: 36px;
    text-transform: capitalize;
    text-align: center;
}

.line {
    height: 1px;
    width: 55px;
    margin: 30px 0;
    background-color: #ffb4ce;
}

@media screen and (max-width: 494px ) {
    .titles {
        font-size: 30px;

    }
}